<template>
  <div class="container">
    <form @submit="preventSubmit" class="form-horizontal">
      <div class="form-group">
        <label class="col-sm-2 control-label">userName</label>
        <div class="col-sm-10">
          <input
            required
            type="text"
            class="form-control"
            v-model="userName"
            placeholder="userName"
          />
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label">passWord</label>
        <div class="col-sm-10">
          <input
            required
            type="password"
            class="form-control"
            v-model="passWord"
            placeholder="passWord"
          />
        </div>
      </div>
      <div class="form-group">
        <label class="col-sm-2 control-label">annoyname</label>
        <div class="col-sm-10">
          <input
            required
            type="text"
            class="form-control"
            v-model="annoyname"
            placeholder="annoyname"
          />
        </div>
      </div>
      <div class="form-group">
        <div class="col-sm-offset-2 col-sm-10">
          <button class="btn btn-default" @click="regist">Regist</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'regist',
  data () {
    return {
      userName: '',
      passWord: '',
      allowRegist: '',
      annoyname:''
    }
  },
  methods: {
    regist: function () {
      // 注册方法
      console.log('/api/checkUserName');
      this.$axios.post('/api/checkUserName', { username: this.userName })
        .then((response) => {
          if(!response.data) {
            alert("用户名已存在!")
          }
          else {
            this.$axios.post('/api/regist',{
              username:this.userName,
              userpwd:this.passWord,
              annoyname:this.annoyname
            })
            .then((response)=>{
              console.log(response);
              this.allowRegist = true;
            })
            .catch((err)=> {
              console.log(err);
            })
          }
        })
        .catch((err) => {
          console.log(err);
        })
    },
    preventSubmit: function (e) {
      e.preventDefault();
    }
  },
  watch: {
    allowRegist:function (value) {
      if(value) {
        this.$router.push({path:'/login'});
      }
    }
  },
}
</script>

